/*** Portfolio Website CSS ***/



body {
    font-size: 16px;
}
a {
    color: #bf2a2b;
}
a:hover {
    color: #a72526
}
p {
    font-size: 1rem;
    line-height: 1.4;
}
.color-red {
    color: #bf2a2b;
}
.bg-red {
    background-color: #bf2a2b;
}
.display-5 {
    font-size: 2rem;
}
.navbar-light .navbar-nav .nav-link {
    font-size: 1.25rem;
}
.navbar-light .navbar-nav .nav-link:hover,
.navbar-light .navbar-nav .nav-link.active {
    color: #bf2a2b;
 }
.navbar-title {
    font-size: 1.5rem;
}
.main-container {
    background-image: url("./assets/images/logo-white.png");
    padding-bottom: 30rem;
    background-position: bottom 9rem left;
    background-repeat: no-repeat;
}
footer {
    box-shadow: 0px 0px 3px 1px #ccc;
    min-height: 60px;
}
footer a {
    color: white;
}
footer a:hover {
    color: #CCC;
}