body {
    margin: 0;
    font-family: -apple-system, BlinkMacSystemFont, "Segoe UI", "Roboto", "Oxygen",
    "Ubuntu", "Cantarell", "Fira Sans", "Droid Sans", "Helvetica Neue",
    sans-serif;
    -webkit-font-smoothing: antialiased;
    -moz-osx-font-smoothing: grayscale;
}
code {
    font-family: source-code-pro, Menlo, Monaco, Consolas, "Courier New",
    monospace;
}
.g-card {
    margin: 20px;
}
.g-card-image {
    border-radius: 10px;
    width: 450px;
    box-shadow: 0px 0px 3px 1px #ccc;
}
.g-card-info {
    margin-top: 2rem;
    min-height: 100px;
}
.g-card-title {
    font-size: 1.25rem;
    font-weight: 500;
}
.g-card-sub-title {
    font-size: 1rem;
}
.success-msg {
  color: #57A773;
  padding: 10px 15px;
}
.err-msg {
  color: #EE6352;
  padding: 10px 15px;
}